body {
  margin: 0;
  padding: 0;
  font-family: cursive,Roboto,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
  color:  #4169E1;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.abs, .app-screen, .app-popup-conteiner, .app-popup-wrapper, .app, .app-map {
  position: absolute;
  width: auto;
  height: auto;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0; }

.app-popup-conteiner {
  box-sizing: border-box;
  padding: 12px;
  display: flex;
  align-items: center;
  justify-content: center; }

.app-popup-wrapper {
  pointer-events: none;
  background-position-y: -0.5px;
  background-image: url(../../static/media/appbackground.87d46a61.jpg);
  opacity: 0.8; }

.app-select {
  width: 100%;
  padding-left: 12px;
  padding-right: 12px;
  box-sizing: border-box; }

.app-select-title {
  text-align: left; }
  .app-select-title:after {
    content: ':'; }

.app-select-value {
  text-align: left;
  height: 40px;
  display: flex;
  align-items: center;
  border-bottom: 2px solid #4169E1; }
  .app-select-value span {
    text-align: center;
    width: 100%;
    flex: 1 1; }

.app-select-option {
  font-size: 20px;
  text-align: center;
  line-height: 48px;
  border-bottom: 2px solid #4169E1; }
  .app-select-option:last-child {
    border-bottom: none; }

.app-popup {
  border: 2px solid currentColor;
  color: #4169E1;
  width: 100%;
  z-index: 1; }

.app {
  text-align: center;
  background-image: url(../../static/media/appbackground.87d46a61.jpg); }

.app-game {
  overflow: auto; }

.app-map-gegs {
  position: absolute;
  width: 36px;
  height: 68px;
  background: #4169E1;
  opacity: 0.4;
  margin-left: -18px;
  margin-top: -34px; }
  .app-map-gegs:before, .app-map-gegs:after {
    content: '';
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    border: 34px solid transparent; }
  .app-map-gegs:before {
    left: -18px;
    border-right: 18px solid #4169E1;
    border-left: none; }
  .app-map-gegs:after {
    left: 36px;
    border-left: 18px solid #4169E1;
    border-right: none; }

.app-map-meteor {
  position: absolute;
  width: 57.6px;
  height: 68px;
  margin-left: -28.8px;
  margin-top: -34px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 50px;
  background-position: center; }

.app-btn, .app-menu-btn {
  border: 2px solid currentColor;
  color: #4169E1;
  font-size: 19px;
  line-height: 36px;
  float: left;
  width: 100%;
  box-sizing: border-box; }
  .app-btn .-red, .app-menu-btn .-red {
    color: #4169E1; }

.app-btn-game {
  position: absolute;
  right: 16px;
  bottom: 0;
  overflow: hidden;
  color: #0d763b;
  font-size: 56px;
  line-height: 1;
  width: 56px; }

.app-btn-close {
  position: absolute;
  left: 50%;
  bottom: 0;
  overflow: hidden;
  color: #0d763b;
  font-size: 56px;
  line-height: 1;
  margin-left: -28px;
  width: 56px; }

.app {
  text-align: center;
  background-image: url(../../static/media/appbackground.87d46a61.jpg); }

.app-menu-btn {
  margin-top: 4px;
  margin-bottom: 4px; }

.app-menu {
  box-sizing: border-box;
  padding: 12px;
  display: flex;
  flex-direction: column;
  justify-content: center; }

